.todo {
  width: 90%;
}

.cont_info {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #011627;
}

.cont_info .info {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: left;
  margin-left: 10px;
}
.cont_info .info p {
  width: 100%;
  height: 100%;
  text-align: justify;
  color: white;
}

.cont_info .info2 {
  background-color: #011627;
  width: 30%;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.info2 .img {
  width: 100%;
  height: 100%;
  margin-bottom: 5px;
  margin-top: 5px;
  display: flex;
  justify-content: flex-end;
}
.info2 .img img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 50%;
}
.cont_info .info .btnlinkedin {
  background-color: #011627;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 16px;
}
